* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}

.html {
  height: 100%;
}

.right {
  text-align: right;
}

img {
  height: 30px;
  width: 30px;
  vertical-align: middle;
  margin-right: 11px;
}

input {
  width: 260px;
  height: 30px;
  margin-top: 15px;
  text-indent: 12px;
  margin-bottom: 2em;
  margin-top: 30px;
}

.grey {
  color: rgb(143, 143, 143);
}

.coin__symbol {
  color: black;
  font-weight: 500;
}

.coin__image {
  margin-right: 40px;
}

.red {
  color: #ea3943;
  font-weight: 500;
}

.green {
  color: #2aa878;
  font-weight: 500;
}

.caret {
  margin-right: 58px;
  margin-bottom: -3.5px;
}

.coin__percent {
  margin-top: -22px;
}

table {
  margin-left: auto;
  margin-right: auto;
  width: 950px;
  border-collapse: collapse;
}

th {
  text-align: left;
  border-bottom: 0.5px solid rgb(201, 201, 201);
  border-top: 0.5px solid rgb(201, 201, 201);
  height: 36px;
}

td {
  height: 48px;
  text-align: left;
  border-bottom: 0.5px solid rgb(201, 201, 201);
  border-top: 0.5px solid rgb(201, 201, 201);
}

tr > * + * {
  padding-left: 4em;
}
